.footer {
  
  .footer-top {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid #dae4e9;

    &__logo {
      display: block;
      margin-bottom: 28px;
    }

    &__item {
      margin-bottom: 70px;
      width: 280px;
    }

    &__item-first {
      margin-right: 30px;
      width: 100%;
      max-width: 240px;

      span {
        text-transform: uppercase;
      }
    }

    &__item-title {
      margin-bottom: 25px;
      padding-top: 15px;
      font-size: 16px;
      line-height: 21px;
      text-transform: uppercase;
      color: #1f2044;

      @extend %dmsans-700;
    }

    &__item-link {
      display: block;
    }

    &__item-link + .footer-top__item-link {
      margin-top: 15px;
    }

    &__item-form {
      width: 270px;
    }

    &__item-text {
      margin-bottom: 36px;
    }

    &__form-input {
      padding: 12px 15px;
      width: 100%;
      max-width: 170px;
      border: none;
      font-size: 12px;
      background-color: #fef7f0;

      &::placeholder {
        color: #234969;
      }

      @extend %dmsans-500;
    }

    &__form-btn {
      padding: 12px;
      width: 96px;
      border: none;
      line-height: 22px;
      color: #fff;
      background-color: #a3bbc8;
      transition: all 0.3s;

      &:hover {
        background-color: #234969;
      }

      @extend %dmsans-500;
    }

    &__address,
    &__phone,
    &__email {
      display: block;
      margin-bottom: 15px;
      color: #7b7b7b;
    }
  }

  .footer-bottom {
    display: flex;
    justify-content: space-between;
    padding: 30px 0;
  }
}